@font-face {
    font-family: 'LINESeedKR-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Rg.woff2') format('woff2');
    font-weight: 100;
    font-style: normal;
}

@font-face {
    font-family: 'KOTRAHOPE';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/KOTRAHOPE.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

body {
    margin: 0;
}

.not_logged_massage {
    padding-left: 55px;
}

.not_logged {
    padding-left: 55px;
    list-style: none;
}

.not_logged > p {
    font-family: 'LINESeedKR-Bd';
}

.not_logged > li {
    font-family: 'LINESeedKR-Bd';
    float: left;
}

.SideMenu {
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: 50px;
    display: grid;
    grid-template-rows: 145px 100px 100px auto;
    width: 250px;
    height: auto;
    border: solid black 1px;
}


.header_main {
    background-color: #7895B2;
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.main_Logo > a > img {
    display: block;
    margin: auto;
}

Nav {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: #AEBDCA;
    margin-top: 20px;
    width: 1200px;
    list-style-type: none;
    text-align: center;
    padding: 0;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
}

.NavLink:hover {
    color: #bad7e9;
    background-color: #2b3467;
    transition-property: color, background-color;
    transition-duration: 330ms;
}

.NavLink {
    padding-top: 10px;
    padding-bottom: 10px;
    color: #2b3467;
    text-decoration: none;
    font-size: 20px;
    font-family: 'KOTRAHOPE';
    text-align: center;
    display: block;
}

.main_shopMenu > div >li {
    float: left;
    list-style: none;
    margin-left: 12px;
    margin-top: 5px;
}

.main_shopMenu {
    margin-left: 30px;
}

.main_shopMenu_box {
    display: block;
    border: solid black;
    width: 180px;
    height: 40px;
    border-radius: 20px;
}

.search_text {
    font-family: 'LINESeedKR-Bd';
    padding-left: 3px;
    font-size: 15px;
    width: 100px;
    height: 20px;
}

.search_button {
    padding-left: 0;
    margin-top: -3px;
    cursor: pointer;
    width: 30px;
    height: 30px;
    border: none;
    background-color: transparent;
}